
import {Avatar, Stack, Typography} from "@mui/material";
import {Face4} from "@mui/icons-material";

export default function TeamCard() {

    const memberList = [
        {name: 'Edwards', firstname: 'Sophie', desc: ['suivi de grossesse', 'prparation  la naissance', 'rducation du prine']},
        {name: 'Barret', firstname: 'Julie', desc: ['suivi de grossesse', 'prparation  la naissance', 'suivi gyncologique', 'prescription et pose de contraceptifs', 'acupuncture', 'micronutrition', 'visites  domiciles aprs accouchement et grossesse pathologique']},
        {name: 'Mateu', firstname: 'Stphanie', desc: ['chographie obsttricales']},
        {name: 'Molinet', firstname: 'Elsa', desc: ['suivi gyncologique', 'contraception : prescription et pose', 'INDIBA (tecartherapie)', 'IVG mdicamenteuse  domicile', 'suivi de grossesse', 'prparation  la naissance', 'rducation du prine', 'visite  domicile aprs accouchement et pour grossesse pathologique']}
    ]

    return (
        <Stack flexDirection='row' justifyContent={'center'} gap={2}>
            {memberList.map(member =>
                <Stack
                    width={'100%'}
                    alignItems={'center'}
                    backgroundColor={'white'}
                    gap={4}
                    borderRadius={'10px'}
                    boxShadow={'0 13px 35px -12px rgba(35,35,35,.1)'}
                >
                    <Avatar variant="rounded" sx={{ width: '100%', height: 200 }}>
                        <Face4 fontSize='large' />
                    </Avatar>
                    <Typography color={'secondary'} fontSize={'18px'} fontWeight={'bold'}>
                        {member.firstname} {member.name}
                    </Typography>
                    <Stack p={'0 20px 20px 20px'}>
                        {member.desc.map(desc =>
                            <Typography width={'100%'}>- {desc}</Typography>
                        )}
                    </Stack>
                </Stack>
            )}
        </Stack>
    );
}
